import videoRender from '@/components/zaiui-common/basics/video-render';
import _tool from '@/utils/tools.js';
export default {
	name: 'trends-list',
	components: {
		videoRender
	},
	props: {
		list_data: {
			type: Array,
			default: () => {
				return []
			}
		},
		isMin: {
			type: Number,
			default: 0
		},
		isMax: {
			type: Number,
			default: 0
		},
		viewBtn: {
			type: Boolean,
			default: false
		}
	},
	methods: {
		listTap(tap,data,index) {
			this.$emit(tap, {
				data,
				index
			});
		},
		imgTap(img,arr,index) {
			this.$emit('imgTap', {
				img,
				arr,
				index
			});
		},
		getMinToMax(index) {
			let isMin = this.isMin, isMax = this.isMax;
			if(isMin == 0 && isMax == 0 ) {
				return true;
			} else if (isMin > 0 && isMax == 0) {
				if(index >= isMin) {
					return true;
				} else {
					return false;
				}
			} else if (isMin == 0 && isMax > 0) {
				if(index < isMax) {
					return true;
				} else {
					return false;
				}
			} else if (isMin > 0 && isMax > 0) {
				if(index >= isMin && index < isMax) {
					return true;
				} else {
					return false;
				}
			} else {
				return false;
			}
		},
		videoErrorCallback(e) {
			uni.showToast({
			    title: e.target.errMsg,
			    duration: 2000,
				icon: 'none'
			});
		}
	}
}
